<template>
	<div class="Me">
		<div class="wallet">
			<img src="@/assets/MyWallet.png" alt="">
			<div class="myWallet">
				<div class="name">{{$t('mywallet')}}</div>
				<div class="add">
					<div class="address">{{address}}</div>
					<div class="copy" @click="copy(userAddress)">
						<img src="@/assets/copy.png">
					</div>
				</div>
			</div>
		</div>
		<div class="box">
			<div class="my" v-for="(el, i) in item" :key="i">
				<div class="icon">
					<img :src="el.icon">
				</div>
				<div class="name">{{el.name}}</div>
				<div class="num">{{el.value | NumFormat}}</div>
				<van-button type="info" @click="harvest" :disabled="btnDisabled" :loading="btnLoading" v-if="el.btn"
					class="btn" plain color="#1F75D9">{{el.btn}}</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		Toast
	} from 'vant';
	import LP from '@/assets/LP.png'
	import IN from '@/assets/IN.png'
	import {
		NumFormat
	} from '@/util/filter.js'
	import BigToSmall from '@/util/BNUtils.js'
	export default {
		name: 'heads',
		data() {
			return {
				btnDisabled: false,
				btnLoading: false,
				timer: null
			}
		},
		filters: {
			NumFormat
		},
		computed: {
			userAddress() {
				return this.$store.state.address
			},
			address() {
				let formatAddress = this.$util.plusXing(this.$store.state.address || '', 3, 4, '.', 3)
				return formatAddress
			},
			item: {
				get() {
					let lp = this.$store.state.myLp
					let INSum = this.$store.state.getAccountInfoSum['2']
					INSum = (INSum / BigToSmall.pow(1, 18))
					console.log('我的总收益', INSum)
					return [{
							icon: LP,
							name: this.$t('myLP'),
							value: lp
						},
						{
							icon: IN,
							name: this.$t('myIncome'),
							value: INSum,
							btn: this.$t('harvest')
						}
					]
				},
				set(v) {
					return v
				}
			}
		},
		destroyed() {
			clearInterval(this.timer)
		},
		mounted() {
			this.timer = setInterval(() => {
				this.myallLp()
			}, 3000)
		},
		methods: {
			// 复制
			copy(val) {
				let domInput = document.createElement('input');
				domInput.value = val;
				document.body.appendChild(domInput);
				domInput.select();
				document.execCommand("Copy");
				Toast(this.$t('success'))
				domInput.remove()
			},
			// 我质押的总lp
			async myallLp() {
				let poolContractInstance = this.$store.state.poolContractInstance
				let val = this.$store.state.address
				let myTotal = await poolContractInstance.methods.getAccountInfoSum(val).call()
				this.$store.dispatch("ActionsSetGetAccountInfoSum", myTotal);
				let INSum = myTotal['2']
				this.item[1].value = (INSum / BigToSmall.pow(1, 18))
			},
			// 收割
			async harvest() {
				try {
					if (this.item[1].value == 0) return
					let that = this
					this.btnDisabled = true
					this.btnLoading = true
					await this.$store.state.poolContractInstance.methods.harvestAll().send({
						from: that.userAddress
					}).then((res) => {
						Toast(this.$t('success'))
						this.btnDisabled = false
						this.btnLoading = false
						console.log('收割', res)
					})
				} catch (e) {
					console.log('错误', e)
					Toast(this.$t('cancel'))
					this.btnDisabled = false
					this.btnLoading = false
				}


			}
		}
	}
</script>

<style scoped lang="less">
	img {
		width: 100%;
		height: 100%;
		display: block;
	}

	.Me {
		padding: 10px;

		.box {
			display: grid;
			grid-gap: 20px;
			grid-template-columns: 1fr 1fr;
			margin-top: 13px;

			.my {
				background: #fff;
				display: flex;
				border-radius: 6px;
				flex-direction: column;
				padding: 14px;

				.icon {
					height: 32px;
					width: 32px;
				}

				.name {
					font-size: 14px;
					margin-top: 15px;
					margin-bottom: 17px;
				}

				.num {
					font-size: 15px;
					font-weight: bold;
					color: #0076DD;
				}

				.btn {
					margin-top: 25px;
					height: 37px;
					width: 100%;
					line-height: 37px;
					text-align: center;
					// border: 1px solid #1F75D9;
					border-radius: 3px;
					font-size: 14px;
					font-weight: 500;
					// color: #1F75D9;
				}
			}
		}

		.wallet {
			height: 157px;
			width: 100%;
			position: relative;

			.myWallet {
				position: absolute;
				top: 61px;
				left: 18px;

				.name {
					font-size: 14px;
					font-weight: 500;
					color: #FFFFFF;
				}

				.add {
					margin-top: 18px;
					display: flex;
					align-items: center;

					.address {
						font-size: 17px;
						font-weight: 500;
						color: #FFFFFF;
						line-height: 1;
					}

					.copy {
						margin-left: 10px;
						height: 13px;
						width: 13px;
					}
				}
			}
		}
	}
</style>
